export default {
	data() {
		return {
			list: [
				{
					group: [
						{
							icon: 'icon-profit',
							title: '我的收益',
							url: '/pages/mine/profit/index',
							isLink: false
						}
					]
				},
				{
					group: [
						{
							icon: 'icon-machine',
							title: '我的交易',
							url: '/pages/mine/machine/index',
							isLink: false
						},
						{
							icon: 'icon-tuandui',
							title: '我的盟友',
							url: '/pages/mine/ally/index',
							isLink: false
						}
					]
				},
				{
					group: [
						{
							icon: 'icon-card',
							title: '我的银行卡',
							url: '/pages/mine/bank-card/index',
							isLink: false
						},
						{
							icon: 'icon-dingdan',
							title: '我的订单',
							url: '/pages/mine/order/index',
							isLink: false
						},
						{
							icon: 'icon-serve',
							title: '客服中心',
							url: '/pages/mine/customer/index',
							isLink: true
						},
						{
							icon: 'icon-setting',
							title: '设置',
							url: `/pages/mine/setting/index`,
							isLink: true
						}
					]
				}
			],
			user_name: '', // 用户名
			is_authentication: '',	// 实名状态
			mobile: '',	// 用户手机
			avatar: '',	// 用户头像
			level: '', // 用户等级
			is_auth_text:''//实名状态名字
		};
	},
	methods: {
		link(url,isLink) {
			if(!isLink && this.is_authentication!=2) {
				uni.showToast({
					title: '请完成实名认证',
					icon: 'none'
				})
				return
			}
			uni.navigateTo({
				url: url
			})
		},
		real_name(){
			if(this.is_authentication==2) return
			uni.navigateTo({
				url:'information/real-name'
			})
		},
		updateLeave(){
			uni.navigateTo({
				url:'updata/index'
			})
		}
	},
	onShow(){
		this.$store.dispatch('getUserInfo',{}).then(res=>{
			this.user_name = res.data.uinfo.realname
			this.is_authentication = res.data.uinfo.is_auth
			this.level = res.data.uinfo.level
			this.avatar = res.data.uinfo.head_img
			this.is_auth_text = res.data.uinfo.is_auth_text
		}).catch(err=>{})
	}
}
